<template>
  <div id='main'>
    <div class="content">
      <!--  -->
      <div class="choose">
        <div style="padding:15px 15px;">
          <el-form :inline="true" :model="search">
            <el-form-item label="">
              <el-input v-model="search.username" placeholder="" clearable></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="search.realName" placeholder="" clearable></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="sumitQuery"></el-button>
            </el-form-item>
            <el-form-item>
              <el-button type="success" @click="add"></el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <!--  -->
      <el-dialog :visible.sync="formVisible" title="" width="33%">
        <el-form ref="form" :model="form" :rules="formRules" label-width="120px">
          <el-form-item label="" prop="instituteId">
            <el-select v-model="form.instituteId" placeholder="" @change="changeSpeciality">
              <el-option v-for="item in consts.instituteList" :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="specialityId">
            <el-select v-model="form.specialityId" placeholder="">
              <el-option v-for="item in specialityList" :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="username" v-if="passwordChangable">
            <el-input v-model="form.username" placeholder="">
              <template slot="prepend">{{usernamePrefix}}</template>
            </el-input>
          </el-form-item>
          <!-- <el-form-item label="" prop="password">
            <el-input v-model="form.password" placeholder="" type="password" :disabled="!passwordChangable"></el-input>
          </el-form-item> -->
          <!-- <el-form-item label="" prop="roleId">
            <el-select v-model="form.roleId" placeholder="">
              <el-option v-for="role in consts.roleList" :key="role.id" :label="role.name" :value="role.id"></el-option>
            </el-select>
          </el-form-item> -->
          <el-form-item label="" prop="realName">
            <el-input v-model.trim="form.realName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="age">
            <el-input v-model.number="form.age" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="gender">
            <el-select v-model="form.gender" placeholder="">
              <el-option :label="''" :value="1"></el-option>
              <el-option :label="''" :value="0"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="formVisible = false"> </el-button>
          <el-button type="primary" @click="submitForm"> </el-button>
        </div>
      </el-dialog>
      <!--  -->
      <v-table :tableData="tableData" :columns="columns" :pagination="pagination" @handleTableChange="find" :operateColumn="operateColumn" :otherHeight="otherHeight"></v-table>
    </div>
    <div style="clear:both"></div>
  </div>

</template>
<script>
import VTable from '../components/VTable.vue'
import consts from '../assets/utils/consts.js'
const { RSA } = require('../assets/utils/rsa.js').crypto

export default {
  name: 'Student',
  components: {
    VTable,
  },
  data() {
    return {
      consts,
      passwordChangable: false,
      formVisible: false,
      specialityList: [],
      otherHeight: 0,
      search: {
        username: '',
        realName: '',
      },
      form: {
        username: '',
        password: '',
        userId: '',
        instituteId: '',
        specialityId: '',
        roleId: 3,
        realName: '',
        age: '',
        gender: '',
      },
      formRules: {
        username: { required: true, message: '', trigger: 'blur' },
        password: {
          validator: (rule, value, callback) => {
            if (this.passwordChangable && !/^.*(?=.{6,})(?=.*\d)(?=.*[A-z!@#$%^&*,.]).*$/.test(value)) {
              callback(new Error('6'))
            }
            callback()
          },
          trigger: 'blur',
        },
        age: { type: 'number', message: '' },
      },
      columns: [
        { prop: 'username', label: '' },
        {
          prop: 'instituteId',
          label: '',
          format: row => `<span>${(row.instituteId && this.mapToInstitute(row.instituteId).name) || ''}</span>`,
        },
        {
          prop: 'specialityId',
          label: '',
          format: row =>
            `<span>${(row.specialityId && this.mapToSpeciality(row.instituteId, row.specialityId).name) || ''}</span>`,
        },
        { prop: 'realName', label: '', width: '' },
        { prop: 'age', label: '', width: '' },
        {
          prop: 'gender',
          label: '',
          format: row => `<span>${row.gender && row.gender === 1 ? '' : '' || ''}</span>`,
        },
      ],
      pagination: {
        current: 1,
        size: 10,
        total: 0,
        sidx: 1,
        sord: 'asc',
        roleId: 3,
      },
      tableData: [],
      operateColumn: {
        width: 150,
        fixed: 'right',
        list: [
          {
            type: 'warning',
            name: '',
            method: row => {
              this.modify(row)
            },
          },
          {
            type: 'danger',
            name: '',
            method: row => {
              this.delete(row.userId)
            },
          },
        ],
      },
    }
  },
  computed: {
    user() {
      return this.$store.state.user
    },
    usernamePrefix() {
      return this.form.instituteId + this.form.specialityId
    },
  },
  mounted() {
    this.find()
  },
  methods: {
    // 
    find(pagination) {
      this.pagination = { ...this.pagination, ...pagination }
      this.$get('api/authority/user', this.pagination).then(({ data }) => {
        this.tableData = data.pageRecords
        this.pagination.total = data.recordAmount
      })
    },
    // 
    add() {
      this.resetForm()
      this.formVisible = true
    },
    // 
    modify(row) {
      this.initForm(row)
      this.formVisible = true
    },
    // 
    delete(id) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      })
        .then(() => {
          this.$delete(`api/authority/user/${id}`).then(() => {
            this.find()
          })
        })
        .catch(() => {})
    },
    sumitQuery() {
      this.find(this.search)
    },
    submitForm() {
      this.$refs.form.validate(valid => {
        if (valid) {
          if (this.form.password && this.passwordChangable) {
            this.form.password = RSA.encrypt(this.form.password)
          }
          this.form.username = this.usernamePrefix + this.form.username
          this.$post('api/authority/user', this.form).then(() => {
            this.find()
            this.formVisible = false
          })
        }
        return valid
      })
    },
    resetForm() {
      this.passwordChangable = true
      this.$refs.form && this.$refs.form.clearValidate()
      Object.keys(this.form).forEach(key => {
        this.form[key] = ''
      })
      this.form.id = null
      this.form.roleId = 3
    },
    initForm(row) {
      this.passwordChangable = false
      this.$refs.form && this.$refs.form.clearValidate()
      this.changeSpeciality(row.instituteId)
      Object.keys(this.form).forEach(key => {
        this.form[key] = row[key]
      })
    },
    changeSpeciality(instituteId) {
      this.specialityList = this.mapToInstitute(instituteId).specialityList
      this.form.specialityId = ''
    },
    mapToRole(roleId) {
      return this.consts.roleList.find(item => item.id === roleId)
    },
    mapToInstitute(instituteId) {
      return this.consts.instituteList.find(item => item.id === instituteId)
    },
    mapToSpeciality(instituteId, specialityId) {
      return this.mapToInstitute(instituteId).specialityList.find(item => item.id === specialityId)
    },
  },
}
</script>

<style>
</style>



